.image-panel-main {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;

  border-width: 4px;

  background-color: #838080;

  height: calc(40vh * 0.9);

  max-height: 800px;
  max-width: 400px;

  transition: 0.005s linear;
}

.image-container {
  width: 100%;
}

.image-selected-true {
  border-style: inset;
  box-shadow: 0px 0px 10px 4px rgba(28, 106, 170, 0.801);
}

.image-selected-false {
  border-style: outset;
}
.image-selected-false:active {
  transform: scale(0.99);
}

.image-panel-main > div {
  position: absolute;
  bottom: 5px;
  overflow: hidden;
  align-self: center;
}

@media (max-width: 900px) {
  .image-panel-main {
    height: auto;
  }
  .image-panel-main > div {
    visibility: hidden;
  }
}
